
const { ccclass, property } = cc._decorator;

@ccclass
export default class TempItem extends cc.Component {

    @property(cc.Toggle)
    public ToggleNode: cc.Toggle = null;

    @property(cc.Sprite)
    public spriteNode: cc.Sprite = null;

    @property(cc.Node)
    public colorNode: cc.Node = null;

    private _data = null;

    private colorArr = [cc.Color.WHITE, cc.Color.RED, cc.Color.YELLOW, cc.Color.BLUE, cc.Color.GREEN]


    public initItem(index, data: any) {
        this._data = data;
        this.ToggleNode.node.active = index >= 3;
        this.ToggleNode.isChecked = this._data.isToggle;
        this.setColor();
        this.setSprite();
    }

    private setSprite() {
        if (parseInt(this._data.spIndex) >= 0) {
            cc.resources.load("images/" + this._data.spIndex, cc.SpriteFrame, function (err, img) {
                this.spriteNode.spriteFrame = img;
                let width = img.getRect().width;
                let height = img.getRect().height;
                let scale = Math.min(100 / width, 100 / height);
                scale = scale > 1 ? 1 : scale;
                this.spriteNode.node.scale = scale;
            }.bind(this));
        }
    }

    private setColor() {
        this.colorNode.color = this.colorArr[this._data.colorValue]
    }


    // 自动播放题干语音开关
    public onToggle(toggle: cc.Toggle): void {
        this._data.isToggle = this.ToggleNode.isChecked;
    }

    private clickColor() {
        this._data.colorValue = this._data.colorValue + 1;
        if (this._data.colorValue > 4) {
            this._data.colorValue = 0
        }
        this.setColor();
    }

}
